'use client';

import type { Answer } from '@/types';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { cn } from '@/lib/utils';
import { Sparkles } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';

interface AnswerCardProps {
  answer: Answer;
}

export function AnswerCard({ answer }: AnswerCardProps) {
  return (
    <Card className={cn(answer.isAiSuggestion && 'border-accent bg-accent/10')}>
      <CardHeader className="flex-row items-center justify-between">
        <div className="flex items-center gap-3">
          {answer.isAiSuggestion ? (
            <Avatar className="h-10 w-10 bg-accent/30 flex items-center justify-center">
                <Sparkles className="h-6 w-6 text-accent-foreground" />
            </Avatar>
          ) : (
            <Avatar className="h-10 w-10">
              <AvatarImage
                src={answer.author.photoURL || ''}
                alt={answer.author.displayName || ''}
              />
              <AvatarFallback>
                {answer.author.displayName?.charAt(0)}
              </AvatarFallback>
            </Avatar>
          )}
          <div>
            <p className="font-semibold text-foreground">
                {answer.author.displayName}
            </p>
            <p className="text-sm text-muted-foreground">
                answered {formatDistanceToNow(answer.createdAt, { addSuffix: true })}
            </p>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <p className="whitespace-pre-wrap">{answer.content}</p>
      </CardContent>
    </Card>
  );
}
