'use client';

import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
} from '@/components/ui/form';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { addAnswer } from '@/lib/store';
import { useAuth } from '@/context/AuthContext';
import { useToast } from '@/hooks/use-toast';
import type { Answer } from '@/types';

const formSchema = z.object({
  content: z.string().min(10, 'Answer must be at least 10 characters long.'),
});

interface AnswerFormProps {
  questionId: string;
  onNewAnswer: (answer: Answer) => void;
}

export function AnswerForm({ questionId, onNewAnswer }: AnswerFormProps) {
  const { user } = useAuth();
  const { toast } = useToast();

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      content: '',
    },
  });

  async function onSubmit(values: z.infer<typeof formSchema>) {
    if (!user) {
      toast({
        title: 'Error',
        description: 'You must be logged in to answer.',
        variant: 'destructive',
      });
      return;
    }

    const newAnswer = (await addAnswer(questionId, {
      content: values.content,
      author: user,
    })) as Answer;
    
    onNewAnswer(newAnswer);
    toast({ title: 'Success!', description: 'Your answer has been posted.' });
    form.reset();
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Your Answer</CardTitle>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <FormField
              control={form.control}
              name="content"
              render={({ field }) => (
                <FormItem>
                  <FormControl>
                    <Textarea
                      placeholder="Share your knowledge with the community..."
                      className="min-h-[120px]"
                      {...field}
                    />
                  </FormControl>
                </FormItem>
              )}
            />
            <Button type="submit" disabled={form.formState.isSubmitting}>
              {form.formState.isSubmitting ? 'Posting...' : 'Post Answer'}
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
}
